<template>
  <div>
    <v-card-title class="text-h5" style="color: #273746"> </v-card-title>
    <v-card elevation="0" outlined>
      <v-card-title>
        {{ nom }}
      </v-card-title>
      <v-card-text>
        <strong>
          <div>
            {{ description }}
          </div>
        </strong>
      </v-card-text>
      <v-divider class="mx-4"> </v-divider>
      <v-card-text>
        <v-chip-group v-model="value" active-class="primary white--text" column>
          <v-chip value="yes"> cumple </v-chip>
          <v-chip value="no"> no cumple </v-chip>
        </v-chip-group>
        

        <div v-if="value == 'no'">
          <br />
          <v-expansion-panels accordion v-model="panel">
            <v-expansion-panel v-for="(item, i) in itemsT" :key="i">
              <v-expansion-panel-header
                >{{ item.number }}) {{ item.specificPoint }}
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <div>
                  <v-card elevation="0" outlined>
                    <v-card-text>
                      <div class="text-right">
                        <v-btn icon @click="openDeleteModal(item)">
                          <v-icon>delete</v-icon>
                        </v-btn>
                      </div>
                      <v-text-field
                        v-model="item.specificPoint"
                        outlined
                        label="Punto específico"
                        required
                        @change="sentChangeItems"
                      ></v-text-field>

                    </v-card-text>
                  </v-card>
                </div>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>

          <!--    Array en espera.....
          Numero de foto...
          punto espacifico... -->

          <!-- <v-select outlined v-model="model" :items="items" required></v-select> -->

          <!--  -->
        </div>
      </v-card-text>
      <div class="text-center" v-if="value == 'no'">
        <v-btn rounded color="primary" dark @click="addPoint()">
          Nuevo punto especifico
        </v-btn>
      </div>
      <br />
    </v-card>

    <v-dialog v-model="dialog" persistent max-width="290">
      <v-card>
        <v-card-title class="text-h5"> Borrar </v-card-title>
        <v-card-text>
          ¿Seguro que desea borrar?, Al borrar se ajustará el
          contador.</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="#273746" text @click="dialog = false"> Cancelar </v-btn>
          <v-btn outlined color="#273746" @click="deleteItem(idItemDelete)">
            Si, Borrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "ItemOilLeaks",
  props: {
    template: Object,
  },
  data() {
    return {
      idItemDelete: null,
      dialog: false,
      itemsT: [],
      description: null,
      nom: null,
      value: null,
      items: null,
      model: null,
      overallPhoto: null,
      panel: null,
    };
  },
  watch: {
    value(e) {
      this.$emit("setOption", e);
    },
    overallPhoto(e) {
      console.log(e);
      this.$emit("setOverallPhoto", e);
    },
    /*  itemsT(e) {
      console.log(e);
      this.$emit("setItemsT", e);
    }, */
    itemsT: {
      deep: true,
      handler() {
        console.log(this.itemsT);
        this.$emit("setItemsT", this.itemsT);
      },
    },
    template() {
      this.start();
    },
  },
  created() {
    this.start();
  },
  methods: {
    start() {
      console.log("datps");
      console.log(this.template);
      this.description = this.template.description;
      this.nom = this.template.nom;
      this.value = this.template.value;
      this.items = this.template.items;
      this.model = this.template.model;
      this.overallPhoto = this.template.overallPhoto;
      if (this.template.itemsT) {
        this.itemsT = this.template.itemsT;
      }
    },
    addPoint() {
      const end = this.itemsT.length + 1;
      this.panel = this.itemsT.length;
      const newPoint = {
        number: end,
        specificPoint: "",
        photoNumber: "",
        specs: "",
      };
      this.itemsT.push(newPoint);
    },
    show() {
      console.log(this.itemsT);
      this.$emit("setItemsT", this.itemsT);
    },
    sentChangeItems() {
      console.log("change");
      //this.$emit("setItemsT", this.itemsT);
    },
    openDeleteModal(item) {
      this.idItemDelete = item;
      this.dialog = true;
      /*    console.log("el item");
      console.log(item); */
    },
    deleteItem(getItem) {
      let temp = this.itemsT.filter((item) => item.number != getItem.number);
      console.log(temp);
      if (temp.length > 0) {
        let count = 0;
        let accommodate = temp.map((item) => {
          count++;
          return {
            number: count,
            photoNumber: item.photoNumber,
            specificPoint: item.specificPoint,
            specs: item.specs,
          };
        });
        this.itemsT = accommodate;
        console.log("accommodate");
        console.log(accommodate);
      }
      if (temp.length == 0) {
        this.itemsT = [];
      }
      this.panel = null;
      this.dialog = false;

      //const result = words.filter(word => word.length > 6);
    },

    /*  change() {
      Soon
      this.$emit("setOption", this.value);
      Father
      @setOption="(value) => (item.value = value)"
    }, */
  },
};
</script>

